import { Component, OnInit } from '@angular/core';
//Router
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
//RXJS
import { BehaviorSubject, forkJoin } from 'rxjs';
import { first, map } from 'rxjs/operators';
//Toastr
import { ToastrService } from 'ngx-toastr';
//Model Device
import { User } from '../../../../partials/layout/tacgui/_forms/tac-user-form/user.model'
//Service//
import { UserService } from '../user.service'
import { PreloadService } from '../../../../partials/layout/tacgui/_fields/field-general-list/preload.service'

@Component({
  selector: 'kt-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

  formParameters = {
    action: 'add',
    data: new User,
    old_data: new User
  }

  // preload = new BehaviorSubject({})

  validation = new BehaviorSubject({})
  loadingForm = new BehaviorSubject(true)

  constructor(
    private toastr:ToastrService,
    private usr_service: UserService,
    private router:Router,
    private route: ActivatedRoute,
    private pre: PreloadService
  ) { }

  ngOnInit() {
    this.formParameters.data.empty()
    this.formParameters.old_data.empty()
    this.route.paramMap.pipe(
      first()
    ).subscribe( params => {
      this.fillForm(+params.get('id'))
    });
  }

  fillForm(id:number){
    // console.log(id)
    this.usr_service.get(id).subscribe( data => {
      // console.log(data)
      if (!data){
        this.toastr.error('Server Error!')
        this.router.navigate(['../../'], { relativeTo: this.route });
        return
      }

      if (!Array.isArray(data.group)) data.group = [(data.group) ? data.group : 0];
      if (!Array.isArray(data.acl)) data.acl = [(data.acl) ? data.acl : 0];
      if (!Array.isArray(data.device_list)) data.device_list = [(data.device_list) ? data.device_list : 0];
      if (!Array.isArray(data.device_group_list)) data.device_group_list = [(data.device_group_list) ? data.device_group_list : 0];

      this.formParameters.data = this.makeClone(data)
      this.formParameters.old_data = this.makeClone(data)

      if (this.formParameters.data.valid_from) {
        let tempDate = this.formParameters.data.valid_from.split(' ')[0].split('-')
        if (tempDate.length) {
          this.formParameters.data.valid_from = {}
          if (tempDate[0]) this.formParameters.data.valid_from.year = parseInt(tempDate[0])
          if (tempDate[1]) this.formParameters.data.valid_from.month = parseInt(tempDate[1])
          if (tempDate[2]) this.formParameters.data.valid_from.day = parseInt(tempDate[2])
        }
      }
      if (this.formParameters.data.valid_until) {
        let tempDate = this.formParameters.data.valid_until.split(' ')[0].split('-')
        if (tempDate.length) {
          this.formParameters.data.valid_until = {}
          if (tempDate[0]) this.formParameters.data.valid_until.year = parseInt(tempDate[0])
          if (tempDate[1]) this.formParameters.data.valid_until.month = parseInt(tempDate[1])
          if (tempDate[2]) this.formParameters.data.valid_until.day = parseInt(tempDate[2])
        }
      }

      //console.log(data)
      forkJoin(
        this.pre.get('/tacacs/user/group/list/', data.group.join(',') || 0, {type:'user', id:data.id}),
        this.pre.get('/tacacs/acl/list/', data.acl.join(',')),
        this.pre.get('/tacacs/device/list/', data.device_list.join(',') || 0),
        this.pre.get('/tacacs/device/group/list/', data.device_group_list.join(',') || 0),
      ).pipe(
        map(([group, acl, device_list, device_group_list]) => {
          // console.log(group, service, acl, device_list, device_group_list)
          let returnData = {}
          if (group) returnData['group'] = group
          if (acl) returnData['acl'] = acl
          if (device_list) returnData['device_list'] = device_list
          if (device_group_list) returnData['device_group_list'] = device_group_list
          return returnData;
        }),
        //first()
      ).subscribe( data => {
        // console.log(data)
        // console.log(this.formParameters.data)
        if (data['group']) {
          this.formParameters.data.group = JSON.parse(JSON.stringify(data['group']));
          this.formParameters.old_data.group = JSON.parse(JSON.stringify(data['group']));
        }
        if (data['acl']) {
          this.formParameters.data.acl = JSON.parse(JSON.stringify(data['acl']));
          this.formParameters.old_data.acl = JSON.parse(JSON.stringify(data['acl']));
        }
        if (data['device_list']) {
          this.formParameters.data.device_list = JSON.parse(JSON.stringify(data['device_list']));
          this.formParameters.old_data.device_list = JSON.parse(JSON.stringify(data['device_list']));
        }
        if (data['device_group_list']) {
          this.formParameters.data.device_group_list = JSON.parse(JSON.stringify(data['device_group_list']));
          this.formParameters.old_data.device_group_list = JSON.parse(JSON.stringify(data['device_group_list']));
        }
        // console.log(this.formParameters.data)
        this.loadingForm.next(false)
      })

    })
  }

  makeClone(src){
    return JSON.parse(JSON.stringify(src));
  }

  save(){
    this.loadingForm.next(true)
    let data = this.makeClone(this.formParameters.data)
    let old_data = this.makeClone(this.formParameters.old_data)

    if (data.valid_from && data.valid_from.year){
      let month = (data.valid_from.month.toString().length == 2) ? data.valid_from.month.toString() : '0'+data.valid_from.month.toString()
      let day = (data.valid_from.day.toString().length == 2) ? data.valid_from.day.toString() : '0'+data.valid_from.day.toString()
      data.valid_from = data.valid_from.year+'-'+month+'-'+day+' 00:00:00'
    }
    if (data.valid_until && data.valid_until.year){
      let month = (data.valid_until.month.toString().length == 2) ? data.valid_until.month.toString() : '0'+data.valid_until.month.toString()
      let day = (data.valid_until.day.toString().length == 2) ? data.valid_until.day.toString() : '0'+data.valid_until.day.toString()
      data.valid_until = data.valid_until.year+'-'+month.toString()+'-'+day+' 00:00:00'
    }

    console.log(old_data)
    if (JSON.stringify(data) == JSON.stringify(old_data)){
      this.toastr.warning('No Changes Detected!')
      this.loadingForm.next(false)
      return;
    }

    if (data.enable == old_data.enable && data.enable_flag == old_data.enable_flag) delete data.enable;
    if (data.pap == old_data.pap && data.pap_flag == old_data.pap_flag) delete data.pap;
    if (data.login == old_data.login && data.login_flag == old_data.login_flag) delete data.login;

    console.log(data)
    data.service = (data.service[0] && data.service[0].id) ? data.service.map( x => x.id) : null
    data.group = (data.group[0] && data.group[0].id) ? data.group.map( x => x.id) : null
    data.acl = (data.acl[0] && data.acl[0].id) ? data.acl[0].id : null
    data.device_list = (data.device_list[0] && data.device_list[0].id) ? data.device_list.map( x => x.id) : null
    data.device_group_list = (data.device_group_list[0] && data.device_group_list[0].id) ? data.device_group_list.map( x => x.id) : null

    this.usr_service.save(data).subscribe( data => {
      console.log(data)
      this.validation.next(data.error.validation)
      if (data.error.status){
        this.loadingForm.next(false)
        return
      }
      if (data.save) {
        this.toastr.success('User Changed!');
        this.router.navigate(['../../'], { relativeTo: this.route });
      } else {
        this.toastr.error('Unknown server error');
      }
      this.loadingForm.next(false)
    })
  }

}
