import { Component, OnInit, Input, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
//Toastr
import { ToastrService } from 'ngx-toastr';

import { TacUserOtpService } from './tac-user-otp.service';

@Component({
  selector: 'kt-tac-user-form',
  templateUrl: './tac-user-form.component.html',
  styleUrls: ['./tac-user-form.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class TacUserFormComponent implements OnInit {

  otpSecretUrl = new BehaviorSubject('')

  @Input() options

  @Input() validation
  @Input() loading

  list = {
    group: {
      multiple: true,
      title: 'User Group',
      title_sidebar: 'User Groups List',
      search: true,
      apiurl: 'api/tacacs/user/group/list/',
    },
    acl: {
      multiple: false,
      title: 'Access Control List',
      title_sidebar: 'ACL List',
      search: true,
      apiurl: 'api/tacacs/acl/list/',
    },
    service: {
      multiple: true,
      title: 'Service',
      title_sidebar: 'Service List',
      search: true,
      apiurl: 'api/tacacs/service/list/',
    },
    device_list: {
      multiple: true,
      title: 'Device List',
      title_sidebar: 'Device List',
      search: true,
      apiurl: 'api/tacacs/device/list/',
    },
    device_group_list: {
      multiple: true,
      title: 'Device Group List',
      title_sidebar: 'Device Group List',
      search: true,
      apiurl: 'api/tacacs/device/group/list/',
    }
  }

  // preloadGroup = new BehaviorSubject({})
  // preloadAcl = new BehaviorSubject({})


  constructor(
    private otp: TacUserOtpService,
    private toastr: ToastrService) { }

  refresh(){
    if(!confirm('You will lost the old key, are you sure?')) return
    this.otpSecretUrl.next(this.options.data.otpUpdate())
    this.getUrl()
  }

  getUrl(){
    let username = this.options.data.username
    let secret = this.options.data.mavis_otp_secret
    // console.log(secret)
    this.otp.getUrl(secret, username).subscribe( data => {
      // console.log(data)
      this.otpSecretUrl.next(data)
    })
  }

  ngAfterViewInit() {

    console.log('after init')
    this.loading.subscribe(() => this.getUrl())

  }

  ngOnInit() {

    this.validation.subscribe(data => {
      //console.log(data)
      for (let key in data) {
        if (data[key]) {
          //console.log(data[key])
          for (let i = 0; i < data[key].length; i++) {
              this.toastr.error(data[key][i]);
          }
        }
      }
    })
    console.log('init')
    this.otpSecretUrl.next(this.options.data.mavis_otp_secret)

  }

  changePasswd(section){
    console.log(section)
    switch (section) {
      case 'login':
        if (this.options.data.login_change)
          this.toastr.warning('User can change login password')
        else
          this.toastr.warning('User can NOT change login password')
        break;
      case 'enable':
        if (this.options.data.enable_change)
          this.toastr.warning('User can change enable password')
        else
          this.toastr.warning('User can NOT change enable password')
        break;
      default:
        this.toastr.warning('Unknown parameter!')
    }
  }

  setGroup(data) {
    // console.log(data)
    this.options.data.group = data
  }
  setSevice(data) {
    // console.log(data)
    this.options.data.service = data
  }
  setAcl(data) {
    // console.log(data)
    this.options.data.acl = data
  }
  setDeviceList(data) {
    // console.log(data)
    this.options.data.device_list = data
  }
  setDevGroupList(data) {
    // console.log(data)
    this.options.data.device_group_list = data
  }

}
