import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = 'api/tacacs/user';

@Injectable({
  providedIn: 'root'
})

export class UserService {

  constructor(private http: HttpClient) {}

  add(user:{}) {
    return this.http.post<any>(API_URL + '/add/', user)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }

  get(id:number) {
    const params = new HttpParams()
    .set('id', id.toString());
    return this.http.get<any>(API_URL + '/edit/', { params })
      .pipe(
        first(),
        map( (data) => {
          return data.user
        } )
      )
  }

  save(user:{}) {
    return this.http.post<any>(API_URL + '/edit/', user)
      .pipe(
        first(),
        map( (data) => {
          return data
        } )
      )
  }
}
