export class User {
  acl: any[]
  chap: number
  // client_ip: number
  created_at: string
  default_service: number
  device_group_list: any[]
  device_list: any[]
  device_list_action: number
  disabled: number
  enable: string
  enable_change: number
  enable_flag: number
  group: any[]
  id: number
  login: string
  login_change: number
  login_flag: number
  manual: string
  manual_beginning: string
  // mavis_otp_digest: string
  // mavis_otp_digits: number
  // mavis_otp_enabled: number
  // mavis_otp_period: number
  mavis_otp_secret: string
  // mavis_sms_enabled: number
  mavis_sms_number: string
  message: string
  ms_chap: string
  // nxos_support: number
  pap: string
  // pap_clone: number
  pap_flag: number
  // priv-lvl: -1
  // server_ip: number
  service: any[]
  updated_at: string
  username: string
  valid_from: any
  valid_until: any

  empty(): void {
    this.acl = []
    this.chap = undefined
    // this.client_ip = null
    this.created_at = undefined
    this.default_service = 1
    this.device_group_list = []
    this.device_list = []
    this.device_list_action = 0
    this.disabled = 0
    this.enable = undefined
    this.enable_change = 0
    this.enable_flag = 1
    this.group = []
    this.id = undefined
    this.login = undefined
    this.login_change = 0
    this.login_flag = 3
    this.manual = undefined
    this.manual_beginning = undefined
    // this.mavis_otp_digest = null
    // this.mavis_otp_digits = null
    // this.mavis_otp_enabled = null
    // this.mavis_otp_period = null
    this.mavis_otp_secret = this.newOtpKey()
    // this.mavis_sms_enabled = null
    this.mavis_sms_number = undefined
    this.message = undefined
    this.ms_chap = undefined
    // nxos_support = undefined
    this.pap = undefined
    // this.pap_clone = undefined
    this.pap_flag = 4
    // priv-lvl = undefined
    // this.server_ip = undefined
    this.service = []
    this.updated_at = undefined
    this.username = undefined
    this.valid_from = undefined
    this.valid_until = undefined
  }

  newOtpKey(){
    let length = 224
    var text = "";
    var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";

    for (var i = 0; i < length; i++)
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    return text;
  }

  otpUpdate(){
    this.mavis_otp_secret = this.newOtpKey()
    return this.mavis_otp_secret
  }

}
