import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map, first } from 'rxjs/operators';

const API_URL = '/mavis/otp/generate/secret/';

@Injectable({
  providedIn: 'root'
})

export class TacUserOtpService {

  objectKeys = Object.keys;

  constructor(private http: HttpClient) {}

  getUrl(secret:string, username:string) {
    //let message = ''
    // console.log(username)
    return this.http.post<any>('api' + API_URL, {secret, username})
      .pipe(
        first(),
        map( (data) => {
          return data.url
        } )
      )
  }

}
